import styles from "./SetTingMenu.module.scss";

function SettingMenu({ data, Apply }) {
	return (
		<form className={styles["wrapper"]} onSubmit={Apply}>
			<ul className={styles["menu-wrapper"]}>
				{data.listItems.map((item, index) => {
					return (
						<li
							key={index}
							onClick={(e) => {
								e.stopPropagation();
							}}
							className={styles["menu-item"]}
						>
							<span>{item.lable}</span>
							{item.item}
						</li>
					);
				})}
			</ul>
			<button type='submit'>apply setting</button>
		</form>
	);
}

export default SettingMenu;
